/* eslint-disable max-lines */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import RequiredMark from '@elliemae/ds-basic/form/RequiredMark';
import memoizeOne from 'memoize-one';
import { ResizeObserver } from 'resize-observer';
import Carousel from './Carousel';

const blockName = 'tabs-tab-bar';

const TabList = aggregatedClasses('div')(
  blockName,
  'list',
  ({ withCarousel }) => ({ carousel: withCarousel }),
);
const TabContainer = aggregatedClasses('button', {
  role: 'tab',
})(blockName, 'tab', ({ isActive, showSeparator }) => ({
  active: isActive,
  separator: showSeparator,
}));
const SelectionIndicator = aggregatedClasses('div')(
  blockName,
  'selection-indicator',
);

class TabBar extends Component {
  constructor(props) {
    super(props);
    this.tabListRef = React.createRef();
    this.resizeObserver = new ResizeObserver(() => this.setIndicatorStyle());
    this.state = { indicatorStyle: null };
    this.setIndicatorStyle = this.setIndicatorStyle.bind(this);
    this.getIndicatorStyles = this.getIndicatorStyles.bind(this);
    this.tabsRef = {};
  }

  componentDidMount() {
    const { showSelectionIndicator } = this.props;
    this.resizeObserver.observe(this.tabListRef.current);
    if (showSelectionIndicator) {
      this.setIndicatorStyle();
    }
  }

  componentDidUpdate(prevProps) {
    const { activeTab, showSelectionIndicator } = this.props;

    if (showSelectionIndicator && prevProps.activeTab !== activeTab) {
      this.setIndicatorStyle(activeTab);
    }
  }

  componentWillUnmount() {
    this.resizeObserver.unobserve(this.tabListRef.current);
  }

  setIndicatorStyle() {
    const { activeTab } = this.props;
    setTimeout(
      () =>
        this.setState({
          indicatorStyle: this.getIndicatorStyles(activeTab),
        }),
      100,
    );
  }

  getActiveTabElement(activeTab) {
    const tabListEl = this.tabListRef.current;

    const tabSelector = `[data-tab-id="${activeTab}"]`;

    return tabListEl && tabListEl.querySelector(tabSelector);
  }

  getIndicatorStyles(activeTab) {
    const { withCarousel } = this.props;
    const activeTabEl = this.getActiveTabElement(activeTab);

    if (!activeTabEl) return null;

    const { clientHeight, clientWidth, offsetTop, offsetLeft } = activeTabEl;
    const { left } = activeTabEl.getBoundingClientRect();
    const leftOff = withCarousel ? left : offsetLeft;
    return {
      height: clientHeight,
      left: `${Math.floor(leftOff)}px`, // using left and top for ie compatibility
      top: `${Math.floor(offsetTop)}px`,
      width: clientWidth + 2,
    };
  }

  handleTabClick = (tabId, e) => {
    const { onTabClick } = this.props;
    e.preventDefault();
    onTabClick(tabId);
  };

  handleKeyPress = (tabId, e, index) => {
    const {
      tabs,
      // onTabClick,
    } = this.props;
    if (e.keyCode === 39) {
      const nextTab = tabs[index + 1];
      if (!nextTab) return;
      // const { props } = nextTab
      // onTabClick(props.tabId)
      this.tabsRef[index + 1].focus();
    }
    if (e.keyCode === 37) {
      const nextTab = tabs[index - 1];
      if (!nextTab) return;
      // const { props } = nextTab
      // onTabClick(props.tabId)
      this.tabsRef[index - 1].focus();
    }
  };

  renderTabs = memoizeOne((activeTab, tabs) => {
    const { showSeparator } = this.props;
    return React.Children.map(
      tabs,
      (tab, index) => {
        const { tabId, title, style, required } = tab.props;
        const isActive = activeTab === tabId;
        return [
          <TabContainer
            key={tabId}
            // eslint-disable-next-line no-return-assign
            ref={(t) => (this.tabsRef[index] = t)}
            aria-controls={tabId}
            aria-selected={isActive}
            classProps={{ isActive, showSeparator }}
            data-tab-id={tabId}
            id={`${tabId}-label`}
            onClick={(e) => this.handleTabClick(tabId, e, index)}
            onKeyDown={(e) => this.handleKeyPress(tabId, e, index)}
            style={style}
            tabIndex={0}
          >
            {title}
            <RequiredMark required={required} />
          </TabContainer>,
        ];
      },
      null,
    );
  });

  render() {
    const {
      id,
      tabs,
      activeTab,
      renderCustomTabBar,
      withCarousel,
    } = this.props;
    const { indicatorStyle } = this.state;

    if (renderCustomTabBar) {
      return renderCustomTabBar({
        innerRef: this.tabListRef,
        withCarousel,
        tabsComponents: this.renderTabs(activeTab, tabs),
      });
    }

    return (
      <TabList
        withCarousel={withCarousel}
        aria-label={id}
        innerRef={this.tabListRef}
        role="tablist"
      >
        {withCarousel ? (
          <Carousel onScroll={this.setIndicatorStyle}>
            {this.renderTabs(activeTab, tabs)}
          </Carousel>
        ) : (
          this.renderTabs(activeTab, tabs)
        )}
        <SelectionIndicator style={indicatorStyle} />
      </TabList>
    );
  }
}

TabBar.defaultProps = {
  showSelectionIndicator: true,
  showSeparator: false,
};

TabBar.propTypes = {
  /** Separator between tabs */
  showSeparator: PropTypes.bool,
  /** Blue border under selected tab */
  showSelectionIndicator: PropTypes.bool,
  /** Tab list id */
  id: PropTypes.string,
  /** Array of tabs */
  tabs: PropTypes.arrayOf(PropTypes.element).isRequired,
  /** Renders a custom tabbar */
  renderCustomTabBar: PropTypes.func,
  /** tabId of active tab */
  activeTab: PropTypes.string,
  /** on tab click callback */
  onTabClick: PropTypes.func,
  /** carousel behavior */
  withCarousel: PropTypes.bool,
};

export default TabBar;
