import React from 'react';
import PropTypes from 'prop-types';
import { TabTypes } from './TabTypes';
import TabsImpl from './TabsImpl';
import DSTab from './DSTab';

const DSTabs = ({
  containerProps = {},
  innerRef,
  animated = true,
  enableMouseEvents = false,
  allowTextSelection = false,
  onTabChange = () => null,
  tabsListAriaLabel = 'Tab list',
  type = TabTypes.NORMAL,
  tabBarExtraContent = undefined,
  children: tabs,
  onlyRenderActiveTab = false,
  activeTab,
  withCarousel = false,
}) => (
  <TabsImpl
    activeTab={activeTab}
    allowTextSelection={allowTextSelection}
    animated={animated}
    containerProps={containerProps}
    enableMouseEvents={enableMouseEvents}
    innerRef={innerRef}
    onlyRenderActiveTab={onlyRenderActiveTab}
    onTabChange={onTabChange}
    tabBarExtraContent={tabBarExtraContent}
    tabsListAriaLabel={tabsListAriaLabel}
    type={type}
    withCarousel={withCarousel}
  >
    {tabs}
  </TabsImpl>
);

DSTabs.propTypes = {
  /** Whether the tab transition should animate or not */
  animated: PropTypes.bool,
  /** Enables tab transition with mouse drag events */
  enableMouseEvents: PropTypes.bool,
  /** Allow text selection when mouse drag events are active */
  allowTextSelection: PropTypes.bool,
  /** Handler when a user clicks on a different tab */
  onTabChange: PropTypes.func,
  /** Tab type */
  type: PropTypes.oneOf(['normal', 'subtabs,']),
  /** Extra content next to the tabs pills */
  tabBarExtraContent: PropTypes.element,
  /** Only renders the active tab in the DOM */
  onlyRenderActiveTab: PropTypes.bool,
  /** Activate carousel behavior */
  withCarousel: PropTypes.bool,
  /** The active tabId */
  activeTab: PropTypes.string,
  /** The active tabId */
  children: PropTypes.arrayOf(
    PropTypes.shape({
      type: PropTypes.oneOf([DSTab]),
    }),
  ).isRequired,
  /** Aria label for the list */
  tabsListAriaLabel: PropTypes.string,
  /** Additional properties for the container */
  containerProps: PropTypes.object,
  /** REF */
  innerRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.any }),
  ]),
};

export default DSTabs;
