import React from 'react';
import PropTypes from 'prop-types';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import TabBar from './TabBar';
import Carousel from './Carousel';

const blockName = 'tabs-tab-bar';

const SubTabList = aggregatedClasses('div')(
  blockName,
  'subtabs-list',
  ({ withCarousel }) => ({ carousel: withCarousel }),
);

const SubTabBar = ({
  withCarousel,
  tabs = [],
  activeTab = '',
  onTabClick = () => null,
}) => (
  <TabBar
    activeTab={activeTab}
    onTabClick={onTabClick}
    renderCustomTabBar={({ tabsComponents, innerRef }) => (
      <SubTabList withCarousel={withCarousel} innerRef={innerRef}>
        {withCarousel ? <Carousel>{tabsComponents}</Carousel> : tabsComponents}
      </SubTabList>
    )}
    showSelectionIndicator={false}
    showSeparator
    tabs={tabs}
  />
);

SubTabBar.propTypes = {
  tabs: PropTypes.arrayOf(PropTypes.element).isRequired,
  activeTab: PropTypes.string,
  onTabClick: PropTypes.func.isRequired,
  withCarousel: PropTypes.bool,
};

export default SubTabBar;
