/* eslint-disable max-lines */
import React, { useRef, useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import DSButton from '@elliemae/ds-basic/Button';
import ChevronRight from '@elliemae/ds-icons/ChevronRight';
import ChevronLeft from '@elliemae/ds-icons/ChevronLeft';
import styled from 'styled-components';
import { useResize } from './useResize';

const CarouselBtn = styled(DSButton)`
  position: absolute;
  z-index: 1;
  margin: 0;
  top: 50%;
  transform: translateY(-50%);
  && {
    width: 16px;
    height: 16px;
  }
  ${(props) => (props.right ? 'right: 0;' : '')}

  && .em-ds-icon {
    background: ${(props) => props.theme.colors.neutral['0']};
    background: radial-gradient(
      circle,
      rgba(255, 255, 255, 1) 50%,
      rgba(255, 255, 255, 0) 100%
    );
    height: 16px;
    width: 16px;
  }
`;

const CarouselWrapper = styled.span`
  position: relative;
  width: 100%;
`;

const ChildrenWrap = styled.div`
  display: flex;
  flex-wrap: nowrap;
  flex: 1;
  overflow: hidden;
`;

function isVisibleElement(el, container) {
  const rect = el.getBoundingClientRect();
  const elemLeft = rect.left + 3;
  const elemRight = rect.right - 3;

  // Only completely visible elements return true:
  const isVisible = elemLeft >= 0 && elemRight <= container.clientWidth;
  return isVisible;
}

const Carousel = ({ children, onScroll }) => {
  const itemsRef = useRef([]);
  const wrapRef = useRef(null);
  const leftRef = useRef(null);
  const rightRef = useRef(null);

  const shouldShowLeft = () =>
    children &&
    children[0] &&
    wrapRef.current &&
    itemsRef.current[0] &&
    !isVisibleElement(itemsRef.current[0], wrapRef.current);
  const shouldShowRight = () =>
    children &&
    children[children.length - 1] &&
    wrapRef.current &&
    itemsRef.current[children.length - 1] &&
    !isVisibleElement(itemsRef.current[children.length - 1], wrapRef.current);

  const [{ right, left, CHEVRON_FOCUS_FLAG }, setShow] = useState({
    right: false,
    left: false,
    CHEVRON_FOCUS_FLAG: false, // flag to replace lack of callback in useState hook
  });
  const recalcChevrons = () =>
    setShow({ right: shouldShowRight(), left: shouldShowLeft() });

  useEffect(() => {
    recalcChevrons();
  }, []);

  useEffect(() => {
    if (CHEVRON_FOCUS_FLAG) {
      // focus opposite chevron when current one hides
      if (!left && right && rightRef.current) {
        rightRef.current.focus();
      } else if (!right && left && leftRef.current) {
        leftRef.current.focus();
      }
      setShow({ right, left, CHEVRON_FOCUS_FLAG: false });
    }
  }, [CHEVRON_FOCUS_FLAG]);

  useResize(recalcChevrons);

  const getVisibleItemsOffset = () =>
    itemsRef.current.reduce((acc, c) => {
      if (isVisibleElement(c, wrapRef.current)) {
        return acc + c.offsetWidth;
      }
      return acc;
    }, 0);

  const rightClick = () => {
    wrapRef.current.scrollLeft += getVisibleItemsOffset();
  };

  const leftClick = () => {
    wrapRef.current.scrollLeft -= getVisibleItemsOffset();
  };

  const handleScroll = (e) => {
    // forces render when element.scrollTop is changed (out of react flow)
    setShow({
      right: shouldShowRight(),
      left: shouldShowLeft(),
      // to trigger focus logic at effect above
      CHEVRON_FOCUS_FLAG: true,
    });
    onScroll(e);
  };

  return (
    <CarouselWrapper>
      {left && (
        <CarouselBtn
          buttonType="link"
          innerRef={leftRef}
          size="s"
          icon={<ChevronLeft size="s" color={['brand-primary', '600']} />}
          onClick={leftClick}
        />
      )}
      {right && (
        <CarouselBtn
          innerRef={rightRef}
          right
          size="s"
          buttonType="link"
          icon={<ChevronRight size="s" color={['brand-primary', '600']} />}
          onClick={rightClick}
        />
      )}
      <ChildrenWrap onScroll={handleScroll} ref={wrapRef}>
        {React.Children.map(children, (child, idx) =>
          React.cloneElement(child, {
            key: 'carousel'.concat(idx),
            ref: (el) => {
              itemsRef.current[idx] = el;
            },
          }),
        )}
      </ChildrenWrap>
    </CarouselWrapper>
  );
};

Carousel.defaultProps = {
  onScroll: () => null,
};

Carousel.propTypes = {
  children: PropTypes.arrayOf(PropTypes.element).isRequired,
  onScroll: PropTypes.func,
};

export default Carousel;
