import React, { Component } from 'react';
import memoizeOne from 'memoize-one';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import SwipeableViews from 'react-swipeable-views';

const blockName = 'tabs-panels';

const Panels = aggregatedClasses('div')(blockName);
const PanelContainer = aggregatedClasses('div')(blockName, 'panel');

const getIndexById = (children, activeId) =>
  children && children.findIndex(item => item.props.tabId === activeId);
const getIdByIndex = (children, index) =>
  children && children[index].props.tabId;

export default class TabsPanels extends Component {
  renderPanels = memoizeOne((activeTab, tabsComponents) => {
    const { animated, onlyRenderActiveTab, allowTextSelection } = this.props;
    return React.Children.map(
      tabsComponents,
      (tab, index) => {
        const { tabId: panelId, style, children: content } = tab.props;
        const isActive = activeTab === panelId;
        const shouldRender = !onlyRenderActiveTab || isActive;
        return (
          <PanelContainer
            key={panelId}
            aria-labelledby={`${panelId}-label`}
            classProps={{ isActive }}
            data-panel-id={panelId}
            hidden={!animated && !isActive}
            onMouseDown={e => {
              const role = e.target.getAttribute('role');
              if (role !== 'tabpanel' && allowTextSelection)
                e.stopPropagation();
            }}
            role="tabpanel"
            style={style}
          >
            {shouldRender && content}
          </PanelContainer>
        );
      },
      null,
    );
  });

  render() {
    const {
      activeTab,
      tabsComponents,
      animated,
      onTabChange,
      enableMouseEvents,
    } = this.props;
    const panels = animated ? (
      <SwipeableViews
        enableMouseEvents={enableMouseEvents}
        index={getIndexById(tabsComponents, activeTab)}
        onChangeIndex={index =>
          onTabChange(getIdByIndex(tabsComponents, index))
        }
      >
        {this.renderPanels(activeTab, tabsComponents)}
      </SwipeableViews>
    ) : (
      this.renderPanels(activeTab, tabsComponents)
    );
    return <Panels>{panels}</Panels>;
  }
}
