import React from 'react';
import PropTypes from 'prop-types';

// This component is for create a specific api for Tabs children
const DSTab = ({ tabId = '', title = '', required = false }) => (
  <div
    aria-labelledby={`${tabId}-label`}
    tabId={tabId}
    title={title}
    required={required}
  />
);

DSTab.displayName = 'DSTab';
DSTab.propTypes = {
  /** unique id for tab */
  tabId: PropTypes.string,
  /** tab title */
  title: PropTypes.string,
  /** Show a required mark next to the title */
  required: PropTypes.bool,
};
export default DSTab;
